import axios from 'axios';
import { formConfigSelector } from 'src/services/pages/selectors';
import { serializeForm } from 'src/utils/utils';
import { recaptchaKeySelector } from 'src/services/options/selectors';


/**
 * @param {String} url
 * @returns {Promise<AxiosResponse<T>>}
 */
const get = (url) => {
  let _url = url;

  if (DEVELOPMENT || IS_DEV_SERVER) _url = _url.replace('v1', 'test');

  return axios.get(_url)
    .then(res => res)
    .catch((e) => {
      console.log(e);
      throw e;
    });
};

const post = (url, data) => {
  let _url = url;

  if (DEVELOPMENT || IS_DEV_SERVER) _url = _url.replace('v1', 'test');

  return axios({
    method: 'POST',
    url: _url,
    data,
  })
    .then(res => res)
    .catch((e) => {
      console.log(e);
      throw e;
    });
};

const runRecaptcha = key => new Promise((resolve, reject) => {
  // TODO: gain captcha access and remove
  if (DEVELOPMENT) return resolve('1');

  grecaptcha.execute(key)
    .then((token) => {
      resolve(token);
    }, (e) => {
      console.log(e);
      reject();
    });
});

export const sendForm = () => (dispatch, getState) => {
  const state = getState();
  const form = document.getElementById('page-form');
  const formConfig = formConfigSelector(state);
  const recaptchaKey = recaptchaKeySelector(state);
  const { url: servicePath, method } = formConfig;

  let url = `${SERVER_URL}${servicePath}`;
  const data = serializeForm(method, form);

  switch (method) {
    case 'GET': {
      url = `${url}?${data}`;

      return runRecaptcha(recaptchaKey)
        .then((token) => {
          url += `&_recaptcha_token=${encodeURIComponent(token)}`;
        })
        .then(() => get(url))
        .then((res) => {
        // eslint-disable-next-line prefer-promise-reject-errors
          if (res.data.error) return Promise.reject({ error: res.data.error });
          if (res.data.length === 0) return Promise.reject();

          return res.data;
        });
    }
    case 'POST': {
      data.title = formConfig.title;

      return runRecaptcha(recaptchaKey)
        .then((token) => {
          data._recaptcha_token = token;
        })
        .then(() => post(url, data))
        .then((res) => {
        // eslint-disable-next-line prefer-promise-reject-errors
          if (res.data.error) return Promise.reject({ error: res.data.error });
          return res.data;
        });
    }
    default:
      return Promise.reject(new Error('Unknown form method'));
  }
};

const http = {
  get,
};


export default http;
