import showdown from 'showdown';
import { Link } from 'react-router-dom';
import { currentPageConfigSelector } from 'src/services/pages/selectors';
import LocalStorage from 'src/utils/LocalStorage';


/**
 * @param {String} path - Ex: 'about-us'
 * @returns {String} - Ex: 'AboutUs'
 */
export const mapPathToPageName = path => path
  .split('-')
  .map(string => `${string[0].toUpperCase()}${string.slice(1)}`)
  .join('');

/**
 * @param {String} pageName - Ex: 'AboutUs'
 * @returns {String} - Ex: 'about-us'
 */
export const mapPageNameToPath = (pageName) => {
  const arr = [];
  let start = 0;

  pageName.split('').forEach((char, i) => {
    if (i !== 0 && char === char.toUpperCase()) {
      arr.push(pageName.slice(start, i).toLowerCase());
      start = i;
    } else if (i === (pageName.length - 1)) {
      arr.push(pageName.slice(start).toLowerCase());
    }
  });

  return `/${arr.join('-')}/`;
};

/**
 * Creates an object with props for a react component.
 * 'option' component may be: react-router 'Link', plain 'a'
 * @param {Object} option - Option object from API responseData(options)
 * @returns {Object}
 */
export const getOptionProps = (option) => {
  const props = { key: option.id };

  // local links
  if (option.target) {
    props.as = Link;
    // props.to = mapPageNameToPath(option.target);
    props.to = loc => ({ ...loc, pathname: mapPageNameToPath(option.target) });
    // external links
  } else if (option.url) {
    props.href = option.url;
    props.target = '_blank';
    props.rel = 'noopener noreferrer';
  } else {
    console.warn(`Option(id: ${option.id}) doesn't have required fields.`);
  }

  return props;
};

export const parseMarkdownToHtml = (markdown) => {
  const converter = new showdown.Converter();
  return converter.makeHtml(markdown);
};

/**
 *
 * @param {HTMLElement} form
 * @param {String} method
 * @returns {string || Object}
 */
export const serializeForm = (method, form) => {
  const data = method === 'POST' ? {} : [];

  for (let i = 0; i < form.elements.length; i += 1) {
    const field = form.elements[i];

    // Some validation
    if (!field.name || field.disabled || field.type === 'file' || field.type === 'reset' || field.type === 'submit' || field.type === 'button') continue;

    // if (field.type === 'select-multiple') {
    //   for (let n = 0; n < field.options.length; n += 1) {
    //     if (field.options[n].selected) {
    //       data.push(`${encodeURIComponent(field.name)}=${encodeURIComponent(field.options[n].value)}`);
    //     }
    //   }
    // }
    if ((field.type !== 'checkbox' && field.type !== 'radio') || field.checked) {
      const value = field.value.trim ? field.value.trim() : field.value;

      if (method === 'GET') {
        data.push(`${encodeURIComponent(field.name)}=${encodeURIComponent(value)}`);
      } else if (method === 'POST') {
        data[field.name] = value;
      }
    }
  }

  if (method === 'GET') return data.join('&');
  if (method === 'POST') return data;
};

/**
 * Show/hide chat widget
 */
export const toggleChat = () => {
  // eslint-disable-next-line camelcase
  if (Tawk_API) Tawk_API.toggle();
};

function gtag(...args) { window.dataLayer.push(args); }

export const runGoogleAnalytics = () => {
  window.dataLayer = window.dataLayer || [];
  gtag('js', new Date());
  gtag('config', 'UA-38853360-1');
};

export const checkAndLoadRecaptcha = (recaptchaKey) => {
  if (document.getElementById('recaptcha-api')) return;

  const onLoad = () => {
    grecaptcha.ready(() => {
    });
  };

  const script = document.createElement('script');

  script.onload = onLoad;
  script.async = true;
  script.defer = true;
  script.id = 'recaptcha-api';
  script.src = `https://www.google.com/recaptcha/api.js?render=${recaptchaKey}`;

  document.body.appendChild(script);
};

export const updatePageTags = () => (dispatch, getState) => {
  const state = getState();
  const pageConfig = currentPageConfigSelector(state);

  const {
    title, seoDescription, seoKeys, seoTitle,
  } = pageConfig;

  if (seoTitle) {
    document.title = seoTitle;
    document.querySelector('meta[property="og:title"]').setAttribute('content', seoTitle);
  } else if (title) document.title = title;

  if (seoDescription) document.querySelector('meta[name=description]').setAttribute('content', seoDescription);
  if (seoKeys) document.querySelector('meta[name=keywords]').setAttribute('content', seoKeys);
};

/**
 * Check current app.js file hash and clear LocalStorage data if needed
 */
export const checkNewVersion = () => {
  if (DEVELOPMENT) return;

  const storedHash = LocalStorage.getItem(LocalStorage.frozenKeys.CURRENT_HASH);
  const appScript = document.querySelector('script[src^="/app."]');

  if (appScript) {
    const arr = appScript.src.split('.');

    const currHash = arr[arr.length - 2];

    if (!storedHash || (currHash !== storedHash)) {
      console.log('Clearing Local Storage data');
      LocalStorage.clearStoredData();
      LocalStorage.storeItem(LocalStorage.frozenKeys.CURRENT_HASH, currHash, true);
    }
  } else {
    console.warn('Can\'t detect app.js file');
  }
};
