const storage = window.localStorage;

class LocalStorage {
  static keys = {
    PAGE_CONFIG: 'page_config',
    OPTIONS: 'options',
    KIOSKS: 'kiosks',
    RETAILERS: 'retailers',
  };

  static frozenKeys = {
    CURRENT_HASH: 'current_hash',
  };

  static storeItem(key, data, skipDate = false) {
    try {
      if (Array.isArray(data) && data.length === 0) {
        console.warn('Empty array received from server');
        return;
      }

      storage.setItem(key, JSON.stringify(data));

      if (!skipDate) LocalStorage.updateItemDate(key);
    } catch (e) {
      console.log(e);
    }
  }

  static removeItem(key) {
    try {
      storage.removeItem(key);
    } catch (e) {
      console.log(e);
    }
  }

  static getItem(key) {
    try {
      if (LocalStorage.isItemExpired(key)) {
        storage.removeItem(key);
        return undefined;
      }
      return JSON.parse(storage.getItem(key));
    } catch (e) {
      console.log(e);
      return undefined;
    }
  }

  static isItemExpired(key) {
    try {
      const frozenKeys = Object.keys(LocalStorage.frozenKeys).map(frozenKey => LocalStorage.frozenKeys[frozenKey]);
      if (frozenKeys.includes(key)) return false;

      const date = JSON.parse(storage.getItem(`${key}_date`));

      return (date === undefined) || ((Date.now() - date) > (1000 * 60 * 60 * 24));
    } catch (e) {
      console.log(e);
      return true;
    }
  }

  static updateItemDate(key) {
    storage.setItem(`${key}_date`, JSON.stringify(Date.now()));
  }

  static clearStoredData() {
    try {
      const expiredKeys = Object.keys(LocalStorage.keys).map(key => LocalStorage.keys[key]);

      Object.keys(storage).forEach((storageKey) => {
        // This iteration needed because pages keys(e.g. 'page_config_Main') created dynamically

        expiredKeys.forEach((expiredKey) => {
          if (storageKey.startsWith(expiredKey)) LocalStorage.removeItem(storageKey);
        });
      });
    } catch (e) {
      console.log(e);
    }
  }
}


export default LocalStorage;
